.footer_bottomLine {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
}
.footer_bottomLine_text {
  font-size: 14px;
  line-height: 17px;
  color: rgb(255, 255, 255, 0.5);
}
.footer_bottomLine_logo {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
}

.footer .learnMore_text {
  color: inherit;
  &::before {
    background-color: rgb(255, 255, 255, 0.5);
  }
}
